@charset "utf-8";
/* 전체 페이지 공통 CSS
- index.html 페이지 전체에 적용됨! */
/* 폰트어썸 */
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.2.1/css/all.min.css);

@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Ubuntu:wght@300;400;500;700&display=swap");
/* 
font-family: 'Libre Baskerville', serif;
font-family: 'Ubuntu', sans-serif;
*/

* {
  /* outline: 0.5px solid aqua; */
}

html,body,ul,ol,dl,dt,dd,p,figure,figcaption,h1,h2,h3,h4 {
  margin: 0;
  padding: 0;
}
body{
  background-color: #be5a4b;
  overflow-x: hidden;
}
/* ul,ol */
ul,ol,li{
  list-style: none;
}

a {
  text-decoration: none;
  color: #000;
}

/* 폰트 사이즈 변수셋팅 */
:root {
  --Lfont: min(7vw, 58px);
  --Mfont: min(5vw, 36px);
  --Sfont: min(3vw, 21px);
}
